nt-size: 1.2em;\r\n    margin-bottom: .3rem;\r\n}\r\n\r\nh6 {\r\n    font-size: 1em;\r\n    margin-bottom: .2rem;\r\n    text-decoration: underline;\r\n}","﻿// Media Queries\r\n$media-desktop: \"screen and (min-device-width: 1200px)\";\r\n$media-mobile-landscape: \"screen and (min-device-width: 640px)\";\r\n\r\n// Theme\r\n$theme-background-color: #0f62bd;\r\n\r\n// Dimensions\r\n$desktop-content-width: 76rem;\r\n\r\n// Colors\r\n$white: #fff;\r\n$black: #000;\r\n$lightgrey: #f8f8f8;\r\n$grey: #aaa;\r\n$darkgrey: #999;\r\n$red: #f80000;\r\n$green: #00c346;\r\n\r\n// Font weights\r\n$font-weight-light: 200;\r\n$font-weight-regular: 400;\r\n$font-weight-bold: 600;\r\n\r\n// Fixed Font\r\n$font-family: monospace, monospace;\r\n$font-size: 100%;\r\n$line-height: 1.6;\r\n\r\n// Font Size\r\n$font-size-small: .9rem;\r\n$font-size-smaller: .8rem;\r\n$font-size-smallest: .7rem;\r\n\r\n// Borders\r\n$border-radius: .4rem;\r\n$border-radius-halved: .2rem;\r\n$layout-border-color: #ddd;\r\n$layout-border-stroke: solid;\r\n$layout-border-width: .0625rem;\r\n$layout-border: $layout-border-width $layout-border-stroke $layout-border-color;\r\n\r\n// Padding\r\n$gutter: 1.2rem;\r\n$padding: .8rem;\r\n$padding-vertical: .8rem 0;\r\n$padding-horizontal: 0 .8rem;\r\n$padding-halved: .4rem;\r\n$padding-halved-vertical: .4rem 0;\r\n$padding-halved-horizontal: 0 .4rem;\r\n\r\n// Animation\r\n$transition-speed: .32s;","/// <reference path=\"../site.scss\">\r\n\r\n$button-blue: #08b5e5;\r\n\r\n.sco_button {\r\n    background: $button-blue;\r\n    border-color: $button-blue;\r\n    border-radius: 3px;\r\n    border-style: solid;\r\n    border-width: 1px;\r\n    color: #fff;\r\n    font-size: 14px;\r\n    font-weight: 600;\r\n    min-height: 28px;\r\n    line-height: 26px;\r\n    outline: none;\r\n    overflow: hidden;\r\n    padding: .4rem .8rem;\r\n    text-align: center;\r\n    text-overflow: ellipsis;\r\n    transition: .4s;\r\n    white-space: nowrap;\r\n    width: 100%;\r\n\r\n    &:ac